import React, { Component } from 'react'
import MembersList from './components/MembersList'
import SignIn from './components/SignIn'
import Icon from './components/icon/Icon'
import CreateMember from './components/CreateMember'

const initState = {
    members: [],
    isSignedIn: true,
    user: {}
}
class App extends Component {

    constructor(){
        super();
        this.state = initState;
    }

    componentDidMount(){
        fetch('https://morning-scrubland-16783.herokuapp.com/api/')
        .then(res => res.json())
        .then(users => {
            this.setState({ members: users })
        })
        .catch(console.log)
    }

    signInChange = ({ loginEmail, loginPassword }) => {
        fetch('https://morning-scrubland-16783.herokuapp.com/api/signin',{
            method: 'post',
            headers: {'Content-Type': 'application/json'},
            body: JSON.stringify({ email: loginEmail , password: loginPassword })
        })
        .then( res => res.json() )
        .then( user => {
            console.log(user);
            if(user.committee){
                this.setState({ user, isSignedIn: true })
            }else{
                document.write(user)
            }
        })
        .catch(console.log);
    }

    handleLogOut = () => {
        this.setState({ isSignedIn: false });
    }
    
    addMember = (user) => {
        if(user.registerEmail && user.registerName){
            fetch('https://morning-scrubland-16783.herokuapp.com/api/create', {
                method: 'post',
                headers: {'Content-Type': 'application/json'},
                body: JSON.stringify({
                    email: user.registerEmail,
                    password: user.registerPassword,
                    location: user.registerLocation,
                    committee: user.registerCommittee,
                    name: user.registerName
                })
            }).then(res => res.json())
            .then(user => {
                if(user.name && user.email){
                    window.location.reload()
                }else{
                    alert(user);
                }
            })
            .catch(console.log)

        }else{
            console.log('Errroooooorrrrrr')
        }
    }

    handleDelete = (id) => {
        const delUser = this.state.members.filter(member => member.id === id)[0];
        fetch('https://morning-scrubland-16783.herokuapp.com/api/member/' + id, {
            method: 'delete',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({ email: delUser.email })
        }).then(res => res.json())
        .then(res => {
            alert(res);
                this.componentDidMount()
        })
        .catch(console.log)
    }

    dummyUser = () => {
        const rand = Math.random()
        fetch('https://morning-scrubland-16783.herokuapp.com/api/create', {
            method: 'post',
            headers: {'Content-Type': 'application/json'},
            body: JSON.stringify({
                email: 'elite'+ rand +'@gmail.com',
                password: 'elite@' + rand,
                location: 'ETE' + rand,
                committee: 'rand' + rand,
                name: 'fake' + rand,
                shares: 1000000000000000
            })
        }).then(res => res.json())
        .then(res => {
            console.log(rand);
            this.componentDidMount()
        })
        .catch(err => alert(err));
    }

    render() {
        return( 
            <div className="container bg-white">
                <div className="row">
                    <div className="col-sm-12 text-center">
                        <Icon />
                    </div>
                </div>
                <div className="row">
                    {
                        this.state.isSignedIn ? 
                        (
                        <div className="col-sm-12 pb-5">
                            <h1 className="py-2 text-primary text-center bg-light">Members List</h1>
                            <button className="btn btn-primary" onClick={this.handleLogOut}>Log Out</button>
                            <MembersList 
                            handleDelete={this.handleDelete} 
                            members={ this.state.members } />
                            <CreateMember handleRegister={this.addMember} />
                        </div>
                        ) : (
                            <div className="col-sm-12 pb-5">
                            <button className="btn btn-warning d-none" onClick={this.dummyUser}>Dummy User</button>
                                <SignIn signInChange={this.signInChange} />
                            </div>
                        )
                        
                    }
                    </div>
            </div> 
        );
    }
}

export default App
