import React from 'react'

const initState = {
    email: '',
    password: '',
    name: '',
    committee: null,
    location: ''
}
class CreateMember extends React.Component{
    state = initState

    handleInputChange = (e) => {
        this.setState({ [e.target.name]: e.target.value })
    }
    
    render(){
        const validateEmail = (email) => {
            const re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
            return re.test(String(email).toLowerCase());
        }
        const handleRegisterClick = () => {
            if( validateEmail(this.state.email)){
                if(this.state.password.length >= 6){
                    this.props.handleRegister({ 
                        registerName: this.state.name,
                        registerLocation: this.state.location,
                        registerCommittee: this.state.committee,
                        registerEmail: this.state.email, 
                        registerPassword: this.state.password 
                    })
                }else{
                    this.props.handleRegister({ 
                        registerName: this.state.name,
                        registerLocation: this.state.location,
                        registerEmail: this.state.email 
                    })
                }
            }else {
                this.props.handleRegister({});
            }
        }
        return(
            <div className="p-5 text-primary text-center bg-light mb-5">
                <h3 className="p-1 text-primary text-center bg-light mb-5">Create a Member</h3>
                <div className="form-group">
                    <input 
                    type="text" 
                    name="name" 
                    className="form-control" 
                    id="name"
                    placeholder="Enter Full Name" 
                    onChange={this.handleInputChange}/>
                    
                    <input 
                    type="email" 
                    name="email" 
                    className="form-control" 
                    id="email" 
                    required
                    aria-describedby="emailHelp" 
                    placeholder="Enter email" 
                    onChange={this.handleInputChange}/>
                    
                    <input 
                    type="text" 
                    name="location" 
                    className="form-control" 
                    id="location"
                    placeholder="Enter Location" 
                    onChange={this.handleInputChange}/>
                </div>
                <div className="form-group ">
                    <input 
                    type="password" 
                    name="password" 
                    className="form-control" 
                    id="password"
                    placeholder="Password" 
                    onChange={this.handleInputChange} />

                    <input 
                    type="text" 
                    name="committee" 
                    className="form-control" 
                    id="committee"
                    placeholder="Enter Role in Committee" 
                    onChange={this.handleInputChange}/>
                </div>
                <button 
                type="submit" 
                className="btn btn-primary" 
                onClick={ handleRegisterClick } 
                >Create Member</button>
            </div>
        )
    }
}

export default CreateMember;