import React, {Component} from "react";

class Member extends Component{
  state = {
    count: 0
  }
  componentDidMount(){
    this.setState({count: this.props.shares});
  }
  changeCount = (id, teckie) => {
    fetch('https://morning-scrubland-16783.herokuapp.com/api/edit/'+id, {
      method: 'put',
      headers: {'Content-Type': 'application/json'},
      body: JSON.stringify({ do: teckie })
    })
    .then(res => res.json())
    .then(shares => {
      this.setState({ count: shares })
    })
  }
  render(){
   const { name, id } = this.props;
      return(
        <div className="member p-3">
          <h3>Name: { name }</h3>
          <h5>Shares: { this.state.count } 
            <button className="btn btn-primary ml-4" onClick={() => {this.changeCount(id, 'add')}}> 
              <i className="material-icons pt-1">add</i>
            </button>   
            <button className="btn btn-primary ml-2" onClick={() => {this.changeCount(id, 'min')}}> 
              <i className="material-icons pt-1">remove</i>
            </button>   
          </h5>
        </div>
      );
  }
}
export default Member;
